
#bg{
    background-image: url(/Images/mainbg1.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 90vh;

}
#frame {
    background-image: url(/Images/frame.png);
    height: 90vh;
    width: 100%;
    
    background-repeat: no-repeat;
    background-size:cover;
    mix-blend-mode:darken;
    
}


@media (max-width: 1020px) {
    #bg{
        background-image: url(/Images/mainbg1.jpeg);
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100vh;
        background-position: 20%;
    
    }
    #frame {
        display:none;
    }

}